            if (this.notesPerString.has(note.string)) {
                let n = this.notesPerString.get(note.string);
                let pos = this.x + n.x;
                switch (requestedPosition) {
                    case NoteXPosition.Left:
                        break;
                    case NoteXPosition.Center:
                        pos += n.noteStringWidth / 2;
                        break;
                    case NoteXPosition.Right:
                        pos += n.width;
                        break;
                }
                return pos;
            }
            return 0;
